
import Group from 'vux/src/components/group/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name:'screenList',
data(){
return{
screenItem:[],
position:"left"
}
},
components:{
Group,
Checklist,
XButton
},
computed:{
screenList(){
return this.$store.state.screenList
},
selectedScreenList(){
return this.$store.state.selectedScreenList
},
},
created(){

},
watch:{
screenList(){
for(let i in this.$store.state.screenList){
this.screenItem.push(
this.$store.state.screenList[i].taskName
)
}
},

screenItem(){
console.log(this.screenItem)
}
},
methods:{

}
}



